IG$.sci_servers = $s.extend(IG$.v$pnl, {
	_ref_ds: function() {
		var me = this,
			req;
			
		req = new IG$._rpc$();
		req.init(me, 
			{
				ack: "scipkg",
				payload: {cmd: "get_svr"},
				mbody: {}
			}, me, function(jdoc) {
				var me = this,
					grd_svr = me.down("[name=grd_svr]");
					
				grd_svr.store.loadData(jdoc);
			});
		req.send();
	},
	
	initComponent: function(){
		var me = this;
		
		$s.apply(me, {
			title: IRm$.r1("L_SCI_SERVERS"),
			padding: 10,
			layout: "fit",
			items: [
				{
					xtype: "panel",
					padding: 10,
					layout: {
						type: "vbox",
						align: "stretch"
					},
					items: [
						{
							xtype: "gridpanel",
							name: "grd_svr",
							store: {},
							flex: 1,
							columns: [
								{
									text: IRm$.r1("B_NAME"),
									flex: 1,
									dataIndex: "name"
								},
								{
									text: IRm$.r1("L_HOST"),
									dataIndex: "server_url",
									flex: 1
								},
								{
									text: IRm$.r1("B_STATUS"),
									width: 80
								}
							],
							tbar: [
								{
									xtype: "button",
									text: "Refresh",
									handler: function() {
										this._ref_ds();
									},
									scope: this
								},
								"->",
								{
									xtype: "button",
									text: "Add",
									handler: function() {
										var me = this,
											dlg = new IG$.sci_svr_inst({
												callback: new IG$.callBackObj(me, function() {
													me._ref_ds();
												})
											});
											
										IG$.checkLogin(me, dlg);
									},
									scope: this
								},
								{
									xtype: "button",
									text: "Remove",
									handler: function() {
										
									},
									scope: this
								}
							]
						}
					]
				}
			],
			listeners: {
				afterrender: function(tobj) {
					this._ref_ds();
				}
			}
		});
		
		IG$.sci_servers.superclass.initComponent.call(this);
	}
});

IG$.sci_svr_inst = $s.extend($s.window, {

	width: 500,
	autoHeight: true,
	layout: "fit",
	padding: 5,
	
	_init: function() {
		
	},
	
	_confirm: function(rec) {
		var me = this,
			lcnt = {},
			req;
		
		if (me.getAllValues(lcnt) == false)
			return;
		
		req = new IG$._rpc$();
		req.init(me, 
			{
				ack: "scipkg",
				payload: {cmd: "reg_svr"},
				mbody: lcnt
			}, me, function(jdoc) {
				var me = this;
				IG$.alertmsg(ig$.appname, IRm$.r1("M_SUCCESS"), null, me, 0, "success");
				
				me.callback && me.callback.execute();
				me.close();
			});
		req.send();
	},
	
	_tst_con: function() {
		var me = this,
			lcnt = {},
			req;
			
		if (me.getAllValues(lcnt) == false)
			return;
			
		lcnt.option = "validate";
		
		req = new IG$._rpc$();
		req.init(me, 
			{
				ack: "scipkg",
				payload: {cmd: "reg_svr"},
				mbody: lcnt
			}, me, function(jdoc) {
				var me = this;
				IG$.alertmsg(ig$.appname, IRm$.r1("M_SUCCESS"), null, me, 0, "success");
			});
		req.send();
	},
	
	initComponent: function(){
		var me = this;
		
		$s.apply(me, {
			title: IRm$.r1("L_SCI_SVR_INST"),
			items: [
				{
					xtype: "panel",
					padding: 10,
					layout: {
						type: "vbox",
						align: "stretch"
					},
					items: [
						{
							xtype: "fieldset",
							layout: {
								type: "vbox",
								align: "stretch"
							},
							title: IRm$.r1("B_GENERAL"),
							items: [
								{
									xtype: "textfield",
									fieldLabel: IRm$.r1("B_NAME"),
									name: "l_name",
									required: 1
								},
								{
									xtype: "textfield",
									fieldLabel: IRm$.r1("L_HOST"),
									name: "l_host_url",
									required: 1
								},
								
								{
									xtype: "fieldcontainer",
									fieldLabel: IRm$.r1("L_SERIAL_KEY"),
									layout: {
										type: "hbox",
										align: "stretch"
									},
									items: [
										{
											xtype: "textfield",
											name: "l_serial_1",
											required: 1
										},
										{
											xtype: "textfield",
											name: "l_serial_2",
											required: 1
										},
										{
											xtype: "textfield",
											name: "l_serial_3",
											required: 1
										},
										{
											xtype: "textfield",
											name: "l_serial_4",
											required: 1
										},
										{
											xtype: "button",
											text: IRm$.r1("L_PASTE"),
											handler: function() {
												
											},
											scope: this
										}
									]
								},
								{
									xtype: "container",
									layout: {
										type: "hbox",
										align: "stretch"
									},
									items: [
										{
											xtype: "container",
											flex: 1
										},
										{
											xtype: "button",
											text: IRm$.r1("B_TEST"),
											handler: function() {
												this._tst_con();
											},
											scope: this
										}
									]
								}
							]
						}
					]
				}
			],
			buttons: [
				"->",
				{
					xtype: "button",
					text: IRm$.r1("B_CONFIRM"),
					handler: function() {
						this._confirm();
					},
					scope: this
				},
				{
					xtype: "button",
					text: IRm$.r1("B_CANCEL"),
					handler: function() {
						this.close();
					},
					scope: this
				}
			],
			listeners: {
				afterrender: function(tobj) {
					tobj._init();
				}
			}
		});
		
		IG$.sci_svr_inst.superclass.initComponent.call(this);
	}
});