IG$.sci_lic = $s.extend(IG$.v$pnl, {
	_init: function() {
		var me = this;
		
		me._get_lic();
	},
	
	_get_lic: function() {
		var me = this,
			req;
			
		req = new IG$._rpc$();
		req.init(me, 
			{
				ack: "scipkg",
				payload: {cmd: "get_keys"},
				mbody: {}
			}, me, function(jdoc) {
				var me = this,
					mkeys = me.down("[name=mkeys]");
					
				mkeys.store.loadData(jdoc);
			});
		req.send();
	},
	
	initComponent: function(){
		var me = this;
		
		$s.apply(me, {
			title: IRm$.r1("L_SCI_MGR_LIC"),
			padding: 10,
			layout: "fit",
			items: [
				{
					xtype: "panel",
					padding: 10,
					layout: {
						type: "vbox",
						align: "stretch"
					},
					items: [
						{
							xtype: "gridpanel",
							name: "mkeys",
							store: {},
							flex: 1,
							columns: [
								{
									text: IRm$.r1("L_LIC_KEY"),
									dataIndex: "serial_key_desc",
									flex: 1
								},
								{
									text: IRm$.r1("B_STATUS"),
									dataIndex: "status",
									width: 60
								},
								{
									text: IRm$.r1("L_INVITE_USER"),
									dataIndex: "invite",
									width: 80
								},
								{
									xtype: "actioncolumn",
									width: 60,
									items: [
										{
											iconCls: "igc-share",
											tooltip: IRm$.r1("L_OBJECT_AUTH"),
											handler: function(grid, rowIndex, colIndex) {
												var me = this,
													rec = grid.store.data.items[rowIndex],
													dlg;
													
												dlg = new IG$.sci_lic_inv({
													callback: new IG$.callBackObj(me, function() {
														
													})
												});
											
												IG$.checkLogin(me, dlg);
											},
											scope: this
										},
										{
											iconCls: "icon-grid-delete",
											tooltip: IRm$.r1("L_REMOVE"),
											handler: function(grid, rowIndex, colIndex) {
												var me = this,
													store = grid.store,
													rec = store.getAt(rowIndex);

												IG$.deleteMetaObject(rec.get("uid"), rec, IG$.callBackObj(me, function() {
													store.removeAt(rowIndex);
												}));
											},
											scope: this
										}
									]
								}
							],
							tbar: [
								"->",
								{
									xtype: "button",
									text: "Refresh",
									handler: function() {
										var me = this;
										
										me._get_lic();
									},
									scope: this
								}
							]
						}
					]
				}
			],
			
			listeners: {
				afterrender: function(tobj) {
					this._init();
				}
			}
		});
		
		IG$.sci_lic.superclass.initComponent.call(this);
	}
});

IG$.sci_lic_inv = $s.extend($s.window, {

	width: 500,
	height: 450,
	layout: "fit",
	padding: 5,
	
	_init: function() {
		
	},
	
	_confirm: function(rec) {
		var me = this,
			dsource = me.down("[name=dsource]"),
			opt_dbtype = me.down("[name=opt_dbtype]"),
			dbtype_val = opt_dbtype.getValue(),
			dbtype = dbtype_val["cb_dbtype"],
			sel = !rec ? dsource.getSelectionModel().selected : null;
		
		if (dbtype == "db" || me.sel_mode == 1)
		{
			if (rec)
			{
				me.callback && me.callback.execute(rec);
				me.close();
			}
			else if (sel && sel.length)
			{
				me.callback && me.callback.execute(sel.items[0]);
				me.close();
			}
			else
			{
				IG$.ShowError(IRm$.r1("E_SEL_DC"), me); // "Select data connector to continue!", me);
			}
		}
		else
		{
			me.callback && me.callback.execute();
			me.close();
		}
	},
	
	initComponent: function(){
		var me = this;
		
		$s.apply(me, {
			title: IRm$.r1("L_SCI_LIC_INV"),
			items: [
				{
					xtype: "panel",
					padding: 10,
					layout: {
						type: "vbox",
						align: "stretch"
					},
					autoScroll: true,
					items: [
						{
							xtype: "fieldset",
							layout: {
								type: "vbox",
								align: "stretch"
							},
							title: IRm$.r1("B_GENERAL"),
							items: [
								{
									xtype: "textfield",
									fieldLabel: IRm$.r1("B_NAME"),
									name: "l_name"
								},
								{
									xtype: "textfield",
									fieldLabel: IRm$.r1("L_HOST"),
									name: "l_host_url"
								},
								{
									xtype: "textfield",
									fieldLabel: IRm$.r1("L_USERID"),
									name: "l_user_id"
								}
							]
						},
						{
							xtype: "fieldset",
							layout: {
								type: "vbox",
								align: "stretch"
							},
							title: IRm$.r1("B_OPTIONS"),
							items: [
								{
									xtype: "checkbox",
									fieldLabel: IRm$.r1("L_ALLOW_POST"),
									boxLabel: IRm$.r1("B_ENABLED"),
									name: "allow_post"
								}
							]
						},
						{
							xtype: "textarea",
							fieldLabel: IRm$.r1("L_MESSAGE"),
							name: "l_user_msg"
						},
						{
							xtype: "container",
							layout: {
								type: "hbox",
								align: "stretch"
							},
							items: [
								{
									xtype: "button",
									text: IRm$.r1("L_DEPRIVATE")
								},
								{
									xtype: "container",
									flex: 1
								},
								{
									xtype: "button",
									text: IRm$.r1("B_SEND"),
									handler: function() {
										
									},
									scope: this
								}
							]
						}
					]
				}
			],
			buttons: [
				"->",
				{
					xtype: "button",
					text: IRm$.r1("B_CONFIRM"),
					handler: function() {
						this._confirm();
					},
					scope: this
				},
				{
					xtype: "button",
					text: IRm$.r1("B_CANCEL"),
					handler: function() {
						this.close();
					},
					scope: this
				}
			],
			listeners: {
				afterrender: function(tobj) {
					tobj._init();
				}
			}
		});
		
		IG$.sci_lic_inv.superclass.initComponent.call(this);
	}
});